.radioButton {
  margin-right: 16px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 300;
  display: inline-flex;
  align-items: initial;
  input {
    display: none;
  }
  .simulator {
    display: inline-block;
    width: 12px;
    height: 12px;
    border: 1px solid #999999;
    border-radius: 50%;
    margin-right: 8px;
  }
}
.radioButtonActive {
  margin-right: 16px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 300;
  display: inline-flex;
  align-items: initial;
  input {
    display: none;
  }
  .simulator {
    display: inline-block;
    width: 8px;
    height: 8px;
    border: 3px solid #0ec261;
    border-radius: 50%;
    margin-right: 8px;
  }
}
